@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400&display=swap);
#weather-card {
	color: black;
	width: 60%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 100px;
	background-color: rgba(255, 255, 255, 0.548);
	border-radius: 20px;
	border: 1px solid white;
	padding: 30px;
	text-align: left;
	font-family: 'Karla', sans-serif;
}

h2,
span,
img {
	margin-top: 0;
}

h2 {
	font-weight: 700;
	font-size: 40px;
}

#toggle-units-buttons button {
	background-color: transparent;
	border: none;
}

.inactive {
	color: rgb(255, 255, 255);
}

#toggle-units-buttons button:hover,
.active {
	text-shadow: 0px 0px 2px rgb(253, 255, 149);
	color: rgb(253, 255, 149);
}

#search-bar input {
	width: 33%;
	height: 20px;
	padding-left: 10px;
	padding-top: 3px;
	padding-bottom: 3px;
	background-color: rgba(255, 255, 255, 0.5);
	border: 1px solid white;
	border-radius: 10px;
	font-family: 'Karla', sans-serif;
}

#search-bar {
	margin-top: 20px;
	font-family: 'Karla', sans-serif;
}

#search-bar button {
	margin-top: 10px;
	background-color: transparent;
	color: white;
	border: 1pt solid white;
	font-family: 'Karla', sans-serif;
	font-size: 20px;
	border-radius: 5px;
}

#error-message {
	margin-top: 10px;
	color: rgba(255, 0, 0, 0.678);
	font-style: italic;
}

#search-bar button:hover {
	box-shadow: 0px 0px 5px white;
}

#App {
	width: 100%;
	height: 100%;
	text-align: center;
	transition: background-image 1.2s ease-in-out;
	background-repeat: no-repeat;
	background-size: cover;
	overflow: hidden;
}

.Thunderstorm {
	background-image: url(/weather-app/static/media/thunderstorm.182314b8.jpg);
}

.Drizzle {
	background-image: url(/weather-app/static/media/drizzle.3c4daab9.jpg);
}

.Rain {
	background-image: url(/weather-app/static/media/rain.e5a0f910.jpg);
}

.Snow {
	background-image: url(/weather-app/static/media/snow.067c3897.jpg);
}

.Clear {
	background-image: url(/weather-app/static/media/clear.bfdf54bb.jpg);
}

.Clouds {
	background-image: url(/weather-app/static/media/cloudy.2759fc40.jpg);
}

.Mist,
.Smoke,
.Haze,
.Dust,
.Fog,
.Sand,
.Ash,
.Squall,
.Tornado {
	background-image: url(/weather-app/static/media/atmosphere.6d0a3eba.jpg);
}

.Generic {
	background-color: rgb(46, 168, 206);
}

html,
body,
#root {
	width: 100%;
	height: 100%;
	margin: 0;
}

